import React, { useState } from "react";
import "./css/common.css";
import Header from "./components/header/header.js";
import Modal from "./components/modal.js";
import KakaoMap from "./components/kakaoMap.js";
import eduCenterData2 from "./data/eduCenterData2.js";

function App() {
  const [modal, setModal] = useState(false);
  const [centerInfo, setCenterInfo] = useState(eduCenterData2);
  const [getMap, setGetMap] = useState({});
  const [centerMapTitle, setCenterMapTitle] = useState("애터미 교육센터");
  const [centerMapOffice, setCenterMapOffice] = useState("KR");

  return (
    <div className="App">
      <Header
        centerMapTitle={centerMapTitle}
        centerMapOffice={centerMapOffice}
      />
      <KakaoMap
        setGetMap={setGetMap}
        centerInfo={centerInfo}
        setModal={setModal}
      />
      {modal === true ? (
        <Modal getMap={getMap} centerInfo={centerInfo} setModal={setModal} />
      ) : null}
    </div>
  );
}

export default App;
