/* global kakao */
import React from "react";
import $ from "jquery";
function Card({ pos, getMap }) {
  return (
    <li>
      <a
        href="#none"
        onClick={() => {
          const moveLatLon = new kakao.maps.LatLng(pos.lat, pos.lng);

          getMap.setLevel(6);
          getMap.panTo(moveLatLon);

          const closeOverlay = () => {
            mapCustomOverlay.setMap(null);
          };

          const $wrap = $('<div class="overlay_info" />');
          const $title = $('<div class="title" />').text(pos.centerName);
          const $close = $('<div class="btn-close" title="닫기" />')
            .html('<i class="xi-close"></i>')
            .click(closeOverlay);
          const $body = $('<div class="desc" />');
          const $address = $('<span class="address" />').text(
            pos.address1 + pos.address2
          );

          $wrap.append($title).append($body);
          $body.append($address);
          $title.append($close);

          const content = $wrap[0];

          const mapCustomOverlay = new kakao.maps.CustomOverlay({
            position: moveLatLon,
            content: content,
            xAnchor: 0.5,
            yAnchor: 1.3,
          });

          mapCustomOverlay.setMap(getMap);

          kakao.maps.event.addListener(getMap, "zoom_changed", function () {
            const level = getMap.getLevel();

            if (level > 4) {
              mapCustomOverlay.setMap(null);
            } else {
              mapCustomOverlay.setMap(getMap);
            }
          });
        }}
      >
        <strong>{pos.centerName}</strong>
        <br></br>
        {pos.address1}
        {pos.address2}
      </a>
    </li>
  );
}

export default Card;
